import React from 'react';
import { Modal, Form, Input } from 'antd';

const PasswordForm = ({ visible, onCreate, onCancel, id }) => {
  const [form] = Form.useForm();
  return (
    <Modal
      visible={visible}
      title=" "
      okText=""
      cancelText=""
      onCancel={onCancel}
      onOk={() => {
        form
          .validateFields()
          .then((values) => {
            form.resetFields();
            values._id = id;
            onCreate(values);
          })
          .catch((info) => {
            console.log('  :', info);
          });
      }}
    >
      <Form
        form={form}
        layout="vertical"
        name="form_in_modal"
        initialValues={{
          modifier: 'public'
        }}
      >
        <Form.Item
          name="newPassword"
          label=" "
          rules={[
            {
              required: true,
              min: 6,
              message: "     6 !"
            }
          ]}
          hasFeedback
        >
          <Input type="password" />
        </Form.Item>
        <Form.Item
          name="confirmPassword"
          label=" "
          dependencies={['newPassword']}
          hasFeedback
          rules={[
            {
              required: true,
              min: 6,
              message: "     6 !"
            },
            ({ getFieldValue }) => ({
              validator(rule, value) {
                if (!value || getFieldValue('newPassword') === value) {
                  return Promise.resolve();
                }

                return Promise.reject('  !');
              }
            })
          ]}
        >
          <Input type="password" />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default PasswordForm;
